export default function throttle(fn, threshhold) {
    // 
    var last
    // 
    var timer 
    //  250ms
    threshhold || (threshhold = 500)
    //  threshhold  fn 
    return function () {  
      //  fn
      var context = this
      var args = arguments 
      var now = +new Date()
      //  fn  threshhold
      //  fn
      if (last && now < last + threshhold) {
        clearTimeout(timer) 
        //  fn
        timer = setTimeout(function () {
          last = now
          fn.apply(context, args)
        }, threshhold)
      //  fn
      } else {
        last = now
        fn.apply(context, args)
      }
    }
}